.pricing-card {
  min-height: 100vh;
  width: 100vw;
  overflow: hidden;
  font-family: Montestrat-Regular;
  background: url("/assets/graphics/bg-top.svg"), url("/assets/graphics/bg-bottom.svg"), $lightgrey;
  background-repeat: no-repeat;
  background-position: right top, bottom left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 48px 0;
}
.pricing-card__title {
  text-align: center;
  margin: 0 24px 48px 24px;
  color: $darkgrey;
}
.pricing-card__toggle,
.pricing-card .toggle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 56px;
  margin: 0 24px 56px 24px;
}
.pricing-card__toggle__label,
.pricing-card .toggle__label {
  color: $darkgrey;
  opacity: 0.5;
  cursor: pointer;
}
.pricing-card__toggle__switch,
.pricing-card .toggle__switch {
  cursor: pointer;
}
.pricing-card__toggle__switch--outer,
.pricing-card .toggle__switch--outer {
  margin: 0 24px;
  background: $blue-gradient;
  width: 56px;
  height: 32px;
  border-radius: 16px;
  position: relative;
  padding: 4px;
}
.pricing-card__toggle__switch--inner,
.pricing-card .toggle__switch--inner {
  width: 24px;
  height: 24px;
  background-color: $white;
  position: absolute;
  left: 4px;
  top: 50%;
  border-radius: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}
.pricing-card__toggle__switch--monthly .toggle__switch--inner,
.pricing-card .toggle__switch--monthly .toggle__switch--inner {
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 28px;
}
.pricing-card__card-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  padding: 24px;
  margin: 0 24px 56px 24px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  align-items: center;
}
@media (max-width: 1000px) {
  .pricing-card__card-wrapper {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}
.pricing-card__card,
.pricing-card .card {
  background-color: #fff;
  padding: 31px;
  border-radius: 10px;
  max-width: 350px;
  width: 100%;
  -webkit-box-shadow: $box-shadow;
          box-shadow: $box-shadow;
}
.pricing-card__card__title,
.pricing-card .card__title {
  text-align: center;
  color: $darkgrey;
  margin-bottom: 24px;
}
.pricing-card__card__price,
.pricing-card .card__price,
.pricing-card__card .price,
.pricing-card .card .price {
  color: $black;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.pricing-card__card__dollar-sign,
.pricing-card .card__dollar-sign {
  color: $black;
  text-align: center;
}
.pricing-card__card__item,
.pricing-card .card__item {
  color: $darkgrey;
  text-align: center;
  border-bottom: 1px solid rgba(0,0,0,0.25);
  padding: 13px 0;
}
.pricing-card__card__item-wrapper,
.pricing-card .card__item-wrapper {
  margin-bottom: 32px;
}
.pricing-card__card__button,
.pricing-card .card__button {
  background: $blue-gradient;
  height: 44px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-decoration: none;
  border-radius: 6px;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
  border: 1px solid $blue-gradient;
  color: $white;
  text-transform: uppercase;
}
.pricing-card__card--blue,
.pricing-card .card--blue {
  background: $blue-gradient;
}
.pricing-card__card--blue .card__price,
.pricing-card .card--blue .card__price,
.pricing-card__card--blue .card__dollar-sign,
.pricing-card .card--blue .card__dollar-sign,
.pricing-card__card--blue .card__item,
.pricing-card .card--blue .card__item,
.pricing-card__card--blue .card__title,
.pricing-card .card--blue .card__title {
  color: $white;
}
.pricing-card__card--blue .card__button,
.pricing-card .card--blue .card__button {
  background: $white;
  color: $blue;
}
.pricing-card__card:nth-child(1),
.pricing-card .card:nth-child(1) {
  height: 0%;
  border-radius: 10px 0 0 10px;
}
.pricing-card__card:nth-child(2),
.pricing-card .card:nth-child(2) {
  border-radius: 10px 10px 10px 10px;
  padding: 55px 31px;
}
.pricing-card__card:nth-child(3),
.pricing-card .card:nth-child(3) {
  height: 0%;
  border-radius: 0 10px 10px 0;
}
@media (max-width: 1000px) {
  .pricing-card__card,
  .pricing-card .card {
    margin-bottom: 32px;
    border-radius: 10px !important;
  }
}
